.loader {
    position: relative;
    margin-top: 35vh;
    top: 0%;
    left: 0%;
    transform: translate(-50%, -50%);
    width: 50px;
    height: 50px;
    background-color: #FF1801;
    border-radius: 30%;
    
    &:after {
      content: '';
      position: absolute;
      border-radius: 30%;
      top: 50%;
      left: 50%;
      border: 0px solid #ececec;
      transform: translate(-50%, -50%);
      animation: loading 1000ms ease-out forwards infinite;
    }
    
  }
  
  @keyframes loading {
    
    0% {
      border: 5px solid #ececec;
      width: 0%;
      height: 0%;
    }
    
    100% {
      border: 8px solid #ececec;
      width: 150%;
      height: 150%;
    }
  }