.container {
    display: flex;
    gap: 3.3333vw;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.card {
    position: relative;
    border: 4px solid black;
    width: 20vw;
    background-color: #ececec;
    user-select: none;
}

.ccard {
    position: relative;
    border: 4px solid black;
    width: 40vw;
    background-color: #ececec;
    user-select: none;
}

.cardhead { 
    padding: none;
}

.cardbody {
    background-color: #e2d8d8;
    padding-top: 0.5vw;
    padding-bottom: 0.5vw;
    display: flex;
    color: black;
    vertical-align: middle;
}

.rightbox {
    margin-right: 0.4vw;
    position: absolute;
    line-height: 1.5vw;
    right: 0%;
    bottom: 2%;
}

.rightboxc {
    margin-right: 0.4vw;
    position: absolute;
    line-height: 1.6vw;
    right: 0%;
    bottom: 2.5%;
}

.points {
    background-color: black;
    color: white;
    padding-top: 3px;
    padding-left: 3px;
    padding-right: 3px;
    padding-bottom: 3px;
}

.namebox {
    margin-left: 0.4vw;
    line-height: 1.5vw;
    background-color: #e2d8d8;
}

.headshot {
    width: 15vw;
    background-color: #ececec;
}

.carbody {
    width: 30vw;
    background-color: #FFFFFF;
}

.number { 
    background-color: #ececec;
    position: absolute;
    top: 5%;
    right: 0%;
    width: 7vw;
}

.carlogo { 
    background-color: #FFFFFF;
    position: absolute;
    top: 5%;
    right: 0%;
    width: 6vw;
}

.cardheadconst { 
    padding: none;
    background-color: #FFFFFF;
    height: 10vw;
    padding-top: 0.6vw;
}

.constname {
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: 2.5vw;
}

.small {
    font-size: 1vw;
}

.big {
    font-size: 1.7vw;
}

.smallc {
    font-size: 1.5vw;
}

.bigc {
    font-size: 2vw;
}