@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Racing+Sans+One&display=swap');

.nav {
  height: 10vh;
  background-color: #ececec;
  color: #FF1801;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 1.5rem 1rem;
  font-family: 'Racing Sans One', cursive;
}

.logo {
  width: 100%;
  height: 100%;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
  font-family: 'Racing Sans One', cursive;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
  font-size: large;
}

.nav li:hover {
  text-decoration: underline;
}

.nav li.active {
  overflow: hidden;
  text-decoration: underline;
  white-space: nowrap;
}

.nav li {
  font-family: 'Racing Sans One', cursive;
  overflow: hidden;
  border-right: .15em solid transparent;
  white-space: nowrap;
}

.twt {
  position: relative;
  background-color: black;
  color: white;
  height: 5vh;
  width: 5vh;
  border-radius: 15px;
}

.text {
  left: 27%;
  position: relative;
  text-align: center;
  color: white;
}

/*Animations*/

@keyframes blink {
  from, to { text-decoration-color: transparent }
  50% { text-decoration-color: #FF1801 }
}